const positiveResult = {
  title: '!',
  text: ' !',
  img: 'https://pp.userapi.com/c319025/v319025798/39e2/Rk_6PLxLMqc.jpg'
};
const regularResult = {
  title: '!',
  text: ',    .',
  img: 'https://img.gazeta.ru/files3/189/3469189/orangesred.jpg'
};
const negativeResult = {
  title: '!',
  text: '     !',
  img: 'data:image/png;base64,'
};

export function getUserResults(userInfo) {
  if (userInfo.aValue > 0 && userInfo.bValue > 0) {
    return positiveResult;
  }

  if (userInfo.aValue < 0 && userInfo.bValue < 0) {
    return negativeResult;
  }

  return regularResult;
}



